body {
    margin:0;
    padding:0;
    width:100%;
    overflow: hidden;
    height:100%;
    display: flex;  /*enables flex content for its children*/
    /* box-sizing: border-box; */
  }

  

   h2{
    /* text-align:center; */
    padding-bottom:0;

  }
/* The Modal (background) */
.popup {
    position: relative;
  display: inline-block;
  cursor: pointer;
    -webkit-animation-name: fadeIn; /* Fade in the background */
    -webkit-animation-duration: 1s;
    animation-name: fadeIn;
    animation-duration: 1s
    /* background-color: rgb(0,0,0); Fallback color */
    /* background-color: rgba(0,0,0,0.4); Black w/ opacity */
  }
  
  .popup .popup-content {
    visibility: hidden;
    width: 160px;
    background-color: rgb(26, 24, 24);
    color: rgb(15, 15, 15);
    text-align: center;
    border-radius: 6px;
    padding: 8px 0;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -80px;
  }

  /* Popup arrow */
.popup .popup-content::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: rgb(221, 181, 3);
  }
  /* The Close Button */
  .close {
    color: rgb(14, 13, 13);
    float: bottom;
    font-size: 28px;
    font-weight: bold;
    background-color: burlywood
  }
  
  .close:hover,
  .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }

  /* Add Animation */
@-webkit-keyframes slideIn {
    from {right: -300px; opacity: 0} 
    to {right: 0; opacity: 1}
  }
  
  @keyframes slideIn {
    from {right: -300px; opacity: 0}
    to {right: 0; opacity: 1}
  }
  
  @-webkit-keyframes fadeIn {
    from {opacity: 0} 
    to {opacity: 1}
  }
  
  @keyframes fadeIn {
    from {opacity: 0} 
    to {opacity: 1}
  }

  .flex-container {
    display: flex;
    justify-content:space-between     /* background-color: #f1f1f1; */
  }
  
  .flex-container > div {
    /* background-color: DodgerBlue; */
    /* color: white; */
    width:100%;
    margin: 10px;
    text-align: center;
    line-height: 75px;
    font-size: 30px;
  }


  html {
    height: 100%;
}

.column {
    height: 100%;  /*allows both columns to span the full height of the browser window*/
    display: flex;
    flex-direction: column;  /*places the left and right headers above the bottom content*/
}

#left {
    flex: 1;  /*makes sure that content is not cut off in a smaller browser window*/
}

#right{
    flex: 3
}

.top-left {
    flex-shrink: 0;
    height:50px;

    background-color: #f1be16e7
}

.top-right {
    flex-shrink: 0;
    height:50px;

    display: inline-flex;
    background-color: #643c01;    

}

ul {
    display: inline-flex;
    list-style: none;
}

.bottom {
    background-color: #aaa;

    flex-shrink: 1;  /*ensures that the container will take up the full height of the parent container*/
    overflow-y: auto;  /*adds scroll to this container*/
}
#attribute{
    overflow-y: auto;  /*adds scroll to this container*/
    display:block
}

#map {
    padding: 0;
    flex-shrink: 1;
    margin: 0;
    top:0;
    left:0;
    right:0;
    bottom: 0;
    width: 100%;
    height:100%;
  }